.card-savings .menu-content {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.card-savings .menu-content::before,
.card-savings .menu-content::after {
    content: "";
    display: table;
}

.card-savings .menu-content::after {
    clear: both;
}

.card-savings .menu-content li {
    display: inline-block;
}

.card-savings .menu-content a {
    color: #fff;
}

.card-savings .menu-content span {
    position: absolute;
    left: 50%;
    top: 0;
    font-size: 10px;
    font-weight: 700;
    font-family: "Open Sans";
    -webkit-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
}

.card-savings .wrapper {
    /* background-color: #fff; */
    min-height: 255px;
    position: relative;
    overflow: hidden;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2);
}

.card-savings .wrapper:hover .data {
    -webkit-transform: translateY(0);
    transform: translateY(0);
}

.card-savings .data {
    position: absolute;
    bottom: 0;
    width: 100%;
    -webkit-transform: translateY(calc(70px + 1em));
    transform: translateY(calc(70px + 1em));
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.card-savings .data .content {
    padding: 1em;
    position: relative;
    z-index: 1;
    min-height: 22vh;
}

.card-savings .author {
    font-size: 12px;
}

.card-savings .title {
    margin-top: 10px;
}

.card-savings .text {
    height: 70px;
    margin: 0;
}

.card-savings input[type="checkbox"] {
    display: none;
}

.card-savings input[type="checkbox"]:checked+.menu-content {
    -webkit-transform: translateY(-60px);
    transform: translateY(-60px);
}

.img-card .wrapper {
    /* background: url(https://source.unsplash.com/1600x900/?nature) 20% 1% / cover no-repeat; */
